import React from 'react'

export default function Shop() {
    return (
        <section className='bg-white py-3 lg:py-10 overflow-hidden'>

            <div className='py-3 lg:py-20 mt-1 relative'>

                <div className='container grid grid-cols-12 gap-8 relative z-10'>

                    <div className='col-span-12 lg:col-span-6 flex flex-col justify-center order-1 lg:order-[initial] text-center lg:text-start'>

                        <button className='bg-[#45B8EA] w-fit text-white px-20 py-2 hidden lg:block rounded-[25px] text-[30px] -rotate-3 font-bold uppercase'>shop</button>

                        <h1 className='text-[25px] font-bold mb-4 mt-10'>

                            No te preocupes por el <span className='text-[#45B8EA] font-extrabold italic'>SEO</span>, no hagas cuentas con<span className='mx-2 text-[#45B8EA] font-normal' style={{ fontFamily: 'myFont' }}>Google Add</span>

                        </h1>

                        <h1 className='text-[25px] text-[#58291E] font-bold mb-4'>
                            cafés
                            En nuestra shop los distintos <span className='mx-1 text-[#45B8EA] font-normal' style={{ fontFamily: 'myFont' }}>cafés</span> se posicionarán de manera orgánica a través de las valoraciones de los usuarios, siempre primando el consumo de proximidad.
                        </h1>

                        <h1 className='text-[25px] text-[#58291E] font-bold mb-4'>

                            No somos intermediarios, tus ventas son directas a tu cliente, solo aportamos las herramientas de automatización logística y de pago.

                        </h1>

                    </div>

                    <div className='col-span-12 lg:col-span-6 flex items-center justify-center flex-col'>


                        <button className='bg-[#45B8EA] w-fit text-white px-20 py-2 mb-10 block lg:hidden rounded-[25px] text-[30px] -rotate-3 font-bold uppercase'>shop</button>


                        <img src={require('./row-bg.png')} className='max-h-[535px] max-w-full' />

                    </div>

                </div>

            </div>

        </section>
    )
}
