import { faArrowDown } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import React from 'react'

export default function Hero() {
    return (
        <section className='container grid grid-cols-12 gap-5'>

            <div className='relative col-span-12 lg:col-span-6 flex items-center justify-center'>

                <img src={require('../../../../assests/images/hero-one.png')} alt='' className='' />

            </div>

            <div className='relative col-span-6 hidden lg:flex items-center justify-center'>

                <img src={require('../../../../assests/images/hero-two.png')} alt='' className='' />

            </div>

            <div className='lg:hidden flex flex-col justify-center items-center col-span-12 my-10 font-sans'>

                <button className='bg-[#43B8EA] flex items-center text-[20px] text-white font-medium mb-5 p-3 px-14 rounded-[30px] '>

                    <FontAwesomeIcon icon={faArrowDown} />

                    <span className='ms-3'>
                        Descargar App
                    </span>

                </button>

                <button className='p-3 px-12 bg-transparent text-[#43B8EA] border-2 border-[#43B8EA] font-medium rounded-[30px]'>
                    Contacto
                </button>

            </div>

        </section>
    )
}
